<template>
    <table class="table tablesorter" :class="tableClass">
        <thead :class="theadClasses">
        <tr>
            <slot name="columns" :columns="columns">
                <th v-for="column in columns" :key="column">{{ column }}</th>
            </slot>
        </tr>
        </thead>
        <tbody :class="tbodyClasses">
        <tr v-for="(item, index) in data" :key="index">
            <slot :row="item" :index="index">
                <td
                    v-for="(column, index) in colsWithValue(item)"
                    :key="index">
                    {{ itemValue(item, column) }}
                </td>
            </slot>
        </tr>
        <tr v-if="addRow">
            <td colspan="8"/>
            <td>
                <base-button type="primary" size="sm" style="right: 0;" :disabled="isDisableButton" outline @click="handleClick">
                    
                </base-button>
            </td>
        </tr>
        </tbody>
    </table>
</template>
<script>
export default {
    name: 'base-table',
    props: {
        columns: {
            type: Array,
            default: () => [],
            description: 'Table columns'
        },
        data: {
            type: Array,
            default: () => [],
            description: 'Table data'
        },
        type: {
            type: String, // striped | hover
            default: '',
            description: 'Whether table is striped or hover type'
        },
        theadClasses: {
            type: String,
            default: '',
            description: '<thead> css classes'
        },
        tbodyClasses: {
            type: String,
            default: '',
            description: '<tbody> css classes'
        },
        addRow: {
            type: Boolean,
            default: false
        },
        isDisableButton: {
            type: Boolean,
            default: true
        }
    },
    computed: {
        tableClass() {
            return this.type && `table-${this.type}`;
        },
        colsWithValue() {
            return (row) => {
                return this.columns.filter(column => this.hasValue(row, column))
            }
        }
    },
    methods: {
        hasValue(item, column) {
            return item[column.toLowerCase()] !== 'undefined';
        },
        itemValue(item, column) {
            return item[column.toLowerCase()];
        },
        handleClick() {
            this.$emit("fromBaseTable", "addNewRow")
        }
    }
};
</script>
<style></style>
